import { Component, OnInit,Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';


@Component({
  selector: 'app-operational-status-reason',
  templateUrl: './operational-status-reason.component.html',
  styleUrls: ['./operational-status-reason.component.scss']
})
export class OperationalStatusReasonComponent implements OnInit {
  rejectReason:any ;
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<OperationalStatusReasonComponent>,
  ) { }

  ngOnInit(): void {
    console.log(this.data);
  }
  saveReason(){
    this.dialogRef.close(this.rejectReason);
  }
}
