import { Component, OnInit, ViewChild } from '@angular/core';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { CookieService } from 'ngx-cookie-service';
import { GenMaintService } from 'src/app/api/controllers/GenMaint';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-operational-maintanance-history',
  templateUrl: './operational-maintanance-history.component.html',
  styleUrls: ['./operational-maintanance-history.component.scss']
})
export class OperationalMaintananceHistoryComponent implements OnInit {

  unitAllocationHeader: any
  unitAllocationData: any;
  gridSourceData: any;
  maintainHistoryData: any;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  histroySearch: string;
  subCategoryName: string;
  categoryName: string;
  assetName: any;
  tempMaintenanceList: any = [];
  search: any = {};

  public categorySettings: any = {
    singleSelection: true,
    text: "Search Category",
    selectAllText: 'Select All',
    unSelectAllText: 'UnSelect All',
    enableSearchFilter: true,
    badgeShowLimit: 3,
    // lazyLoading: true
  };
  public subCategorySettings = {
    singleSelection: true,
    text: "Search Sub Category",
    selectAllText: 'Select All',
    unSelectAllText: 'UnSelect All',
    enableSearchFilter: true,
    badgeShowLimit: 3,
    // lazyLoading: true
  };
  subCategoriesData: any = [];
  categoriesData: any = [];
  historyStatus: any;

  constructor(
    private genMaintService: GenMaintService,
    private cookieService: CookieService,
    private datePipe: DatePipe
  ) { }

  ngOnInit() {
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.unitAllocationHeader = [
      {
        text: 'maintanance record no',
        datafield: 'recordNo',
        width: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'title',
        datafield: 'maintTitle',
        minwidth: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'main category',
        datafield: 'categoryName',
        minwidth: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'main subcategory',
        datafield: 'subCategoryName',
        minwidth: 200,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
      },
      {
        text: 'planned maintanance date',
        datafield: 'plannedMaintenanceDate',
        cellsrenderer: cellsrenderer,
        minwidth: 200,
        renderer: columnrenderer
      }, {
        text: 'actual maintanance',
        datafield: 'actualMaintDate',
        cellsrenderer: cellsrenderer,
        minwidth: 200,
        renderer: columnrenderer
      }, {
        text: 'on time / delayed',
        datafield: 'historyStatus',
        // cellsrenderer: cellsrenderer,
        minwidth: 200,
        cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell"><span class="dots rt-1 ' + getClassName(value) + '"></span>' + value + '</div>'
        },
        renderer: columnrenderer
      }, {
        text: 'responsible',
        datafield: 'performedbyAssociationStaffName',
        cellsrenderer: cellsrenderer,
        minwidth: 200,
        renderer: columnrenderer
      }, {
        text: 'status',
        datafield: 'maintenanceStatusName',
        cellsrenderer: cellsrenderer,
        minwidth: 200,
        renderer: columnrenderer
      }, {
        text: 'cost',
        datafield: 'totalBilledAmount',
        cellsrenderer: cellsrenderer,
        minwidth: 200,
        renderer: columnrenderer
      }];
    this.getMaintenaceHistory();
    this.getAllSubCategory()
    this.getAllCategory();

  }
  getMaintenaceHistory() {
    let params: any = {};
    params.apartmentId = parseInt(this.cookieService.get('apartmentId'));
    this.genMaintService.getAllGenMaintenancePlanByApartmentId(params).subscribe((res: any) => {
      if (res) {
        if (res && res.length > 0) {
          res.filter(val => {
            if (!val.actualMaintDate || !val.plannedMaintenanceDate) {
              val.historyStatus = '';
              return;
            }
            if (val.actualMaintDate < val.plannedMaintenanceDate) {
              val.historyStatus = 'Ontime'
            } else {
              val.historyStatus = 'Delayed'
            }
          })
        }
        let responeData: any = []
        responeData = res;
        if(responeData && responeData.length >0 ){
          responeData.filter(val =>{
           val.actualMaintDate = val.actualMaintDate?this.datePipe.transform(val.actualMaintDate,'MM-dd-yyyy'):'';
           val.plannedMaintenanceDate =val.plannedMaintenanceDate?this.datePipe.transform(val.plannedMaintenanceDate,'MM-dd-yyyy'):'';
          })
        }
        this.tempMaintenanceList = res;        
        this.gridSourceData = {
          localdata: responeData.length > 0 ? responeData : [],
          datatype: "array"
        }
        this.maintainHistoryData = new jqx.dataAdapter(this.gridSourceData);
      }

    })



  }

  getPrintParams(event) {
    this.datagrid.exportdata(event, 'OpMaintananceHistory');
  }
  onSearchFilter() {
    if (this.histroySearch != "") {
      let filterGroup = new jqx.filter();
      let filterOperator = 1;
      let filterValue = this.histroySearch;
      let filterCondition = 'contains';
      let filterData = filterGroup.createfilter('stringfilter', filterValue, filterCondition);
      filterGroup.operator = 'or';
      filterGroup.addfilter(filterOperator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.unitAllocationHeader.forEach(item => {
        if (item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filterGroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }


  searchMaintain() {
    let filterArr: any = [];
    let filterCategoryArr = [];
    let filterSubCategoryArr = [];
    if (this.assetName) {
      if (this.tempMaintenanceList && this.tempMaintenanceList.length > 0) {
        this.tempMaintenanceList.filter(val => {
          if (this.assetName == val.assetName) {
            filterArr.push(val);
          }
        })
      }
      this.finalSearchGrid(filterArr);
    }
    if (this.assetName && this.categoryName) {
      filterArr.filter(val => {
        if (this.categoryName == val.categoryName) {
          filterCategoryArr.push(val);
        }
      });
      this.finalSearchGrid(filterCategoryArr);
    } else if (!this.assetName && this.categoryName) {
      if (this.tempMaintenanceList && this.tempMaintenanceList.length > 0) {
        this.tempMaintenanceList.filter(val => {
          if (this.categoryName == val.categoryName) {
            filterCategoryArr.push(val);
          }
        });
        this.finalSearchGrid(filterCategoryArr);
      }
    }
    if (this.categoryName && this.subCategoryName) {
      filterCategoryArr.filter(val => {
        if (this.subCategoryName == val.subCategoryName) {
          filterSubCategoryArr.push(val);
        }
      });
      this.finalSearchGrid(filterSubCategoryArr);
    } else if (!this.categoryName && this.subCategoryName) {
      if (this.tempMaintenanceList && this.tempMaintenanceList.length > 0) {
        this.tempMaintenanceList.filter(val => {
          if (this.subCategoryName == val.subCategoryName) {
            filterSubCategoryArr.push(val);
          }
        });
        this.finalSearchGrid(filterSubCategoryArr);
      }
    }
    if (!this.assetName && !this.categoryName && !this.subCategoryName) {
      console.log('last');
      this.finalSearchGrid(this.tempMaintenanceList);
    }
  }

  finalSearchGrid(responeData) {
    this.gridSourceData = {
      localdata: responeData.length > 0 ? responeData : [],
      datatype: "array"
    }
    this.maintainHistoryData = new jqx.dataAdapter(this.gridSourceData);
  }
  onItemSelect(item: any) {
    console.log(item);
    this.assetName = item.itemName;
    this.searchMaintain();
  }
  onItemSelectCategory(item: any) {
    this.categoryName = item.itemName;
    this.searchMaintain();
  }

  onItemSelectSubcategories(item: any) {
    console.log(item);
    this.subCategoryName = item.itemName;
    this.searchMaintain();
  }
  OnItemDeSelect(item: any) {
    this.searchMaintain();
  }
  onSelectAll(items: any) {
    console.log(items);
  }
  onDeSelectAll(items: any) {
    console.log(items);
    this.assetName = '';
    this.searchMaintain();
  }
  onDeSelectAllCategory(items: any) {
    console.log();
    this.categoryName = '';
    this.searchMaintain();
  }
  onDeSelectAllSubcategories(items: any) {
    console.log(items);
    this.subCategoryName = '';
    this.searchMaintain();
  }

  onStatusFilter() {
    if (this.historyStatus != "") {
      let filterGroup = new jqx.filter();
      let filterOperator = 1;
      let filterValue = this.historyStatus;
      let filterCondition = 'contains';
      let filterData = filterGroup.createfilter('stringfilter', filterValue, filterCondition);
      filterGroup.operator = 'or';
      filterGroup.addfilter(filterOperator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.unitAllocationHeader.forEach(item => {
        if (item.datafield == 'historyStatus') {

          this.datagrid.addfilter(item.datafield, filterGroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  getAllCategory() {
    this.genMaintService.getAllGenMaintenanceCategory().subscribe((res: any) => {
      if (res) {
        if (res && res.length > 0) {
          res.filter(val => {
            this.categoriesData.push({
              'id': val.genMaintenanceCategoryId,
              'itemName': val.genMaintenanceCategoryName
            });
          })
        }
      }
    });
  }
  getAllSubCategory() {
    this.genMaintService.getAllGenMaintenanceSubCategory().subscribe((res: any) => {
      if (res) {
        if (res && res.length > 0) {
          res.filter(val => {
            this.subCategoriesData.push({
              'id': val.genMaintenanceSubCategoryId,
              'itemName': val.genMaintenanceSubCategoryName
            });
          })
        }
      }
    });
  }
}

function getClassName(value) {
  return value == "Delayed" ? 'normal-status' : value == "Ontime" ? 'low' : '';
}
(window as any).getClassName = getClassName;
