	ngOnInit(): void {
		this.modalService = this.injector.get(ModalService);
		// delete maintenance
		this.sharedService.unitlistdeleteindexcast.subscribe(id => {
			if (id != null) {
				let param: any = {}
				;
				param = {
					genMaintenancePlanId: id,
					deleteBy: parseInt(this.cookieService.get('userId'))
				}
				this.genMaintService.deleteGenMaintenancePlan(param).subscribe((res: any) => {
					// this.getAllMaitainList();
					let pos = this.maintList.map((e) => { return e.id; }).indexOf(id);
				 this.maintList.splice(pos,1);
				 this.totalItems = this.maintList.length;
				 this.ItemEndIndex = this.totalItems;
					setTimeout(() => {
						this.sharedService.setAlertMessage("Maintenance  deleted successfully");
						this.sharedService.setUnitListDeleteIndex(null);
					}, 500)
				}, error => {
					console.log(error);
				});
			}
		});
		this.getStatusList();
		this.getAllMaitainList();
		// this.route.paramMap.subscribe(params => {
		// 	if (params) {
		// 		this.assetId = params.get('id');
		// 	}
		// })
	}

	filterList(value) {
		this.maintList = this.tempMaintList.filter((item: any) => item.categoryName.toString().toLowerCase().indexOf(value.toLowerCase()) > -1
		);
	}
	deleteManitenance(row, type) {

		this.modalService.showConfirmModal(row.id);
	}
	getAllMaitainList() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId')
		};
		// this.isAssetLoaded = true;
		this.genMaintService.getAllGenMaintenancePlanByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
				// this.isAssetLoaded = false;
				this.maintList = res ? res : [];
				this.tempMaintList = this.maintList;
				// if(this.maintList && this.maintList.length > 0){
				// 	this.statusData.filter( status =>{
				// 	this.maintList.filter( val => {
                //     if(status.lookupValueId == val.maintenanceStatusId){
				// 	 val.commentStatus = true;
				// 	 console.log(status.lookupValueId == val.maintenanceStatusId);
				// 	} else{
				// 		val.commentStatus= false;	
				// 	}
				// 	});
				// })

				// }

				this.totalItems = this.maintList.length;
				if (this.totalItems > this.itemLimit) {
					this.ItemEndIndex = this.itemLimit;
				} else {
					this.ItemEndIndex = this.totalItems;
				}
			}
		});
	}
	getStatusList() {
		let queryParamBase: any = {};
		queryParamBase = {
			ApartmentId: this.cookieService.get('apartmentId'),
			LookupTypeId: 190,
		};
		this.lookupService.getLookupValueByLookupTypeId(queryParamBase).subscribe((res: any) => {
			if (res) {
				this.statusData = res ? res : [];
			}
		});
	}

	addComments(lookup, list): void {
		const dialogRef = this.dialog.open(OperationalStatusReasonComponent, {
			// width: '50%',
			panelClass: 'material',
			data: { value: lookup.lookupValueName }
		});

		dialogRef.afterClosed().subscribe(result => {
			console.log('The dialog was closed');
			if (result) {
				console.log(result);
				list.maintenanceStatusId = lookup.lookupValueId;
				list.maintainStatusName = lookup.lookupValueName;
				list.comments = result;
				this.updateMaintanancePlan(list);

			}

		});
	}

