import { Component, OnInit ,ViewChild} from '@angular/core';
import { GenMaintService } from 'src/app/api/controllers/GenMaint';
import { CookieService } from 'ngx-cookie-service';
import { StaffService } from 'src/app/api/controllers/Staff';
import { SharedService } from 'src/app/shared/services/shared.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-maintain-operational-maintanance',
  templateUrl: './maintain-operational-maintanance.component.html',
  styleUrls: ['./maintain-operational-maintanance.component.scss']
})
export class MaintainOperationalMaintananceComponent implements OnInit {
  listOfFiles: any;
  isMaintainedbyStaff:any =true;
  fileList: any;
  selectedFile: any;
  isFileAdded: boolean;
  asset:any ={};
  ckeditor:any ;
  message:any={};
  subCategoriesData:any =[];
  categoriesData:any =[];
  maintain:any ={};
  staffList: any = [];
  currentDate:any =new Date();
  opMaintenancePlanId: string;
  isEdit: boolean;
  assetSettings:any ={};
 
  constructor(
    private genMaintService:GenMaintService,
    private cookieService:CookieService,
    private staffService:StaffService,
    private sharedService:SharedService,
    private router:Router,
    private route: ActivatedRoute,
  ) { }

  ngOnInit(): void {
   this.getAllCategory();
   this.getAllSubCategory();
   this.getAllStaff();
   this.assetSettings = {
    singleSelection: true,
    text: "Search",
    selectAllText: 'Select All',
    unSelectAllText: 'UnSelect All',
    enableSearchFilter: true,
    badgeShowLimit: 3,
    // lazyLoading: true
  };
   this.maintain.categoryId ='';
   this.maintain.subCategoryId ='';
   this.maintain.isAssociationStaff =true;
   this.maintain.performedbyAssociationStaffId ='';
   this.route.paramMap.subscribe(params => {
    if (params) {
      this.opMaintenancePlanId = params.get('id');

    }
  });
  if (this.opMaintenancePlanId) {
    let params: any = {};
    this.isEdit = true;
    params.id = this.opMaintenancePlanId;
    console.log(params);
    this.genMaintService.getGenMaintenancePlanById(params).subscribe((res: any) => {
      console.log(res);
      this.maintain = res[0] ? res[0] : {};

    });
  }

  }

  onItemSelectToStaff(item: any) {
		console.log(item);
		this.maintain.performedbyAssociationStaffId = item.id;
	}

	OnItemDeSelectToStaff(item: any) {
		console.log(item);
	}
	selectedItemsToStaff(selectedItems: any) {
		console.log(selectedItems);
	}
	onSelectAllToStaff(items: any) {
		console.log(items);
	}
	onDeSelectAllToStaff(items: any) {
		console.log(items);
	}
  uploadFile(event){
    this.isFileAdded = true;

    for (var i = 0; i <= event.target.files.length - 1; i++) {
      var selectedFile = event.target.files[i];
      this.fileList.push(selectedFile);
      this.listOfFiles.push({'name':selectedFile.name, 'size':selectedFile.size})
    }

  }

  deleteFile(index){
    // Delete the item from fileNames list
    this.listOfFiles.splice(index, 1);
    // delete file from FileList
    this.fileList.splice(index, 1);

    this.selectedFile = null;

    if(this.fileList.length == 0 && this.listOfFiles.length == 0){
      this.isFileAdded = false;
    }

  }
  onContentChanged(event){
    
  }
  getAllCategory() {
		this.genMaintService.getAllGenMaintenanceCategory().subscribe((res: any) => {
			if (res) {
				this.categoriesData = res ? res : [];
			
			}
		});
	}
  getAllSubCategory() {
		this.genMaintService.getAllGenMaintenanceSubCategory().subscribe((res: any) => {
			if (res) {
				this.subCategoriesData = res ? res : [];
			
			}
		});
	}

	getAllStaff() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId')
		};
		this.staffService.getAllStaffsByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
        // this.staffList = res ? res : [];
        if(res && res.length >0){
          res.filter(val => {
            this.staffList.push({
              'id': val.staffId,
              'itemName': val.firstName
            });
          })
        }			
			}
		});
	}
  addMaintanance(){
    let reqObj:any = {};
    reqObj= {
      "id": 0,
      "apartmentId":  this.cookieService.get('apartmentId'),
      "genMaintenanceId": null,
      "notes": this.maintain.notes,
      "maintenanceStatusId": null,
      "firstMaintDate": null,
      "actualMaintDate": this.maintain.actualMaintDate,
      "lastMaintenanceDate": this.maintain.lastMaintenanceDate,
      "nextPlannedMaintenance": this.maintain.nextPlannedMaintenance,
      "maintenanceTypeId": null,
      "maintenanceByCompany": this.maintain.maintenanceByCompany,
      "maintenanceByPerson": null,
      "maintenanceContactNo": null,
      "maintLaborCost":Number(this.maintain.maintLaborCost),
      "maintPartsCost": Number(this.maintain.maintPartsCost),
      "maintVat":  Number(this.maintain.maintVat),
      "maintWithTax": Number(this.maintain.maintWithTax),
      "maintenanceTotalBilledAmt": Number(this.maintain.totalBilledAmount),
      "comments": this.maintain.comments,
      "description": null,
      "imageId": null,
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date(),
      "updatedBy": 0,
      "updatedOn": new Date(),
      "performedAtLocationId":  null,
      "maintenanceLocation": null,
      "serialNo": null,
      // "customFields": "cf1",
      "customFields": null,
      // "maintCategory": "Mc1",
      "maintCategory":null,
      "maintTitle": this.maintain.maintTitle,
      "plannedMaintenanceDate": this.maintain.plannedMaintenanceDate,
      "totalBilledAmount": Number(this.maintain.totalBilledAmount),
      "alertBeforeDays": Number(this.maintain.alertBeforeDays),
      "categoryId":   Number(this.maintain.categoryId),
      "subCategoryId": Number(this.maintain.subCategoryId),
      "recordNo": Number(this.maintain.recordNo),
      "isAssociationStaff": this.maintain.isAssociationStaff,
      "performedbyAssociationStaffId": this.maintain.performedbyAssociationStaffId? Number(this.maintain.performedbyAssociationStaffId):null
    }
    let params: any = {};
		params.genMaintenancePlan = reqObj;
		this.genMaintService.addGenMaintenancePlan(params).subscribe((res: any) => {
			if (res.message) {
				this.sharedService.setAlertMessage("Operational Maintanance added successfully");
        // this.isAssetLoaded = false;
        this.router.navigate(['ams/operational-maintanance/operational-maintanance-list']);
			}
		});
  }


  updateMaintanance(){

    let reqObj:any = {};

    reqObj= {
      "id": this.maintain.id,
      "apartmentId":  this.cookieService.get('apartmentId'),
      "genMaintenanceId": null,
      "notes": this.maintain.notes,
      "maintenanceStatusId": null,
      "firstMaintDate": this.maintain.lastMaintenanceDate,
      "actualMaintDate": this.maintain.actualMaintDate,
      "lastMaintenanceDate": this.maintain.lastMaintenanceDate,
      "nextPlannedMaintenance": this.maintain.nextPlannedMaintenance,
      "maintenanceTypeId": null,
      "maintenanceByCompany": this.maintain.maintenanceByCompany,
      "maintenanceByPerson": null,
      "maintenanceContactNo": null,
      "maintLaborCost":Number(this.maintain.maintLaborCost),
      "maintPartsCost": Number(this.maintain.maintPartsCost),
      "maintVat":  Number(this.maintain.maintVat),
      "maintWithTax": Number(this.maintain.maintWithTax),
      "maintenanceTotalBilledAmt": Number(this.maintain.totalBilledAmount),
      "comments": this.maintain.comments,
      "description": null,
      "imageId": null,
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date(),
      "updatedBy": parseInt(this.cookieService.get('userId')),
      "updatedOn": new Date(),
      "performedAtLocationId":  null,
      "maintenanceLocation": null,
      "serialNo": this.maintain.serialNo,
      // "customFields": "cf1",
      "customFields": null,
      // "maintCategory": "Mc1",
      "maintCategory":null,
      "maintTitle": this.maintain.maintTitle,
      "plannedMaintenanceDate": this.maintain.plannedMaintenanceDate,
      "totalBilledAmount": Number(this.maintain.totalBilledAmount),
      "alertBeforeDays": Number(this.maintain.alertBeforeDays),
      "categoryId":   Number(this.maintain.categoryId),
      "subCategoryId": Number(this.maintain.subCategoryId),
      "recordNo": Number(this.maintain.recordNo),
      "isAssociationStaff": this.maintain.isAssociationStaff,
      "performedbyAssociationStaffId": this.maintain.performedbyAssociationStaffId? Number(this.maintain.performedbyAssociationStaffId):null
      
    }
    let params: any = {};
		params.genMaintenancePlan = reqObj;
		this.genMaintService.updateGenMaintenancePlan(params).subscribe((res: any) => {
		if ( res && res.message) {
				this.sharedService.setAlertMessage("Operational Maintanance updated successfully");
        // this.isAssetLoaded = false;
        this.router.navigate(['ams/operational-maintanance/operational-maintanance-list']);
			}
		});
  }
  
}
